    line-height: 1.5;
}

html {
    scroll-behavior: smooth;
}
  
h1, h2, h3, h4, h5, figure, p, ol, ul {
    margin: 0;
}
  
ol[role="list"], ul[role="list"] {
    list-style: none;
    padding-inline: 0;
}
  
h1, h2, h3, h4, h5 {
    font-size: inherit;
    font-weight: inherit;
}

img {
    display: block;
    max-inline-size: 100%;
}

body {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 
        'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 
        'Helvetica Neue', sans-serif;
    display: flex;
    position: relative;
}

.sidebar {
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 15px 20px;
    height: 100vh;
    overflow: auto;
    position: absolute;
}

.menu-header, .title {
    display: flex;
    gap: 15px;
    align-items: center;
}

#menu-icon {
    width: 30px;
}

#menu-title {
    font-size: 1.3rem;
    font-weight: 480;
}

#menu-content {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#menu-content > div {
    display: grid;
    grid-template-columns: 250px 30px;
}

.place-container {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 2fr 5fr 2fr;
    grid-template-rows: auto auto;
    row-gap: 5px;
    align-items: center;
}

.place-name {
    grid-column: 1 / 3;
}

.weather-desc {
    padding-left: 10px;
}

.place-temp {
    grid-row: 1 / 3;
}

.place-removal {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.removal-icon {
    width: 22px;
}

.main {
    padding: 20px 30px;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

.header {
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title-logo {
    width: 50px;
}

.title-text {
    font-size: 1.5rem;
    font-weight: 500;
}

#location-name {
    line-height: 1.5;
}

#temperature-toggle {
    padding: 8px 10px;
    border-radius: 15px;
    color: white;
    background-color: navy;
}

.content {
    width: 50%;
    flex: 1;
    display: grid;
    grid-auto-flow: column;
    grid-template: repeat(8, auto) / auto auto;
    justify-content: center;
    column-gap: 100px;
}
.head-address {
    font-size: 1.5rem;
}

.subhead-address {
    font-size: 1.3rem;
}

.place-time {
    font-size: 1.2rem;
}

.place-date {
    font-size: 1.1rem;
}

#location-time {
    grid-row: 2 / 4;
    margin: 10px 0;
}

#weekly-weather {
    grid-row: 4 / 9;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#weekly-weather > div {
    display: grid;
    grid-template-columns: 5fr 3fr 1fr 1fr;
    gap: 20px;
    align-items: center;
}

.day-icon {
    height: 30px;
    justify-self: center;
}

#temperature {
    font-size: 2rem;
    justify-self: center;
    align-self: end;
}

#weather-description {
    justify-self: center;
}

#feels-like {
    justify-self: center;
}

#uv-index {
    margin-top: 20px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,gBAAgB;AACpB;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI;;oCAEgC;IAChC,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,kBAAkB;IAClB,aAAa;IACb,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,iCAAiC;AACrC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kCAAkC;IAClC,6BAA6B;IAC7B,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,UAAU;IACV,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,YAAY;IACZ,sBAAsB;AAC1B;;AAEA;IACI,UAAU;IACV,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,0CAA0C;IAC1C,uBAAuB;IACvB,iBAAiB;AACrB;AACA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,cAAc;AAClB;;AAEA;IACI,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sCAAsC;IACtC,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,oBAAoB;AACxB;;AAEA;IACI,eAAe;IACf,oBAAoB;IACpB,eAAe;AACnB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,gBAAgB;AACpB","sourcesContent":[":root {\n    line-height: 1.5;\n}\n\nhtml {\n    scroll-behavior: smooth;\n}\n  \nh1, h2, h3, h4, h5, figure, p, ol, ul {\n    margin: 0;\n}\n  \nol[role=\"list\"], ul[role=\"list\"] {\n    list-style: none;\n    padding-inline: 0;\n}\n  \nh1, h2, h3, h4, h5 {\n    font-size: inherit;\n    font-weight: inherit;\n}\n\nimg {\n    display: block;\n    max-inline-size: 100%;\n}\n\nbody {\n    font-family: syste